.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #ffffff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

h1 {
  color: #ff6f00;
  text-align: center;
  margin-bottom: 20px;
}

Table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

Table th,
Table td {
  padding: 10px;
  text-align: center;
}

Table th {
  background-color: #ff6f00;
  color: #ffffff;
}

Table td {
  background-color: #ffffff;
}

.Modal label {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.Modal label input[type='checkbox'] {
  margin-right: 10px;
}

.save-button {
  display: block;
  width: 100%;
  padding: 10px;
  text-align: center;
  background-color: #ff6f00;
  color: #ffffff;
  font-weight: bold;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.Points {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-size: 18px;
  color: #ff6f00;
}

.points-icon {
  width: 30px;
  height: auto;
  margin-right: 5px;
}

